.App {
  background-color: #f0f0f0;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}
.App div {
  background-color: #f5f5f5;
  margin: 1em auto;
  padding: 1em;
  width: 80%;
}
a {
  color: inherit;
  font-weight: bold;
  text-decoration: none;
}
footer, header {
  background-color: #ff4500;
}
footer {
  text-align: center;
}
footer *, header * {
  color: #f5f5f5;
}
footer a {
  display: block;
  font-size: 1.25em;
  margin: 1em auto;
}
h2 {
  margin: 0.5em 0;
}
h3 {
  margin: 0.25em 0;
}
header {
  display: grid;
  grid-template-columns: auto 1fr;
  padding: 0.5em 0;
}
header * {
  margin: 0;
  vertical-align: middle;
}
header img {
  height: 3em;
  padding: 0 1em;
}
li {
  line-height: 1.5em;
}
